
.account-nav{font-size:1.08333em;display:flex;list-style:none;margin:10px 0;padding:0;justify-content:stretch
}
.account-nav li{flex:1
}
.account-nav li a{display:block;text-align:center;text-decoration:none;color:inherit;text-transform:uppercase
}
.account-nav li a span{border-bottom:1px solid transparent;padding-bottom:2px
}
.account-nav li a.router-link-exact-active span{border-color:#F8105F
}
.account-header{overflow:auto;padding:12px 15px;display:flex;align-items:center
}
.account-header .account-item{flex:1
}
.menu-link{width:24px;height:24px;background:url(data:image/svg+xml;base64,);background-size:16px 14px;background-repeat:no-repeat;background-position:center center
}
.account-name{font-weight:500
}
.account-item{line-height:15px
}
.account-item .identicon{float:left;padding:2px;border:1px solid #979797;border-radius:11px;margin-right:7px
}
.account-item .identicon svg{display:block;width:26px;height:26px
}
