
.error{color:red
}
.overlay-dialog{background:#fff;position:absolute;top:0;left:0;right:0;bottom:0;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;padding:20px;text-align:center;opacity:0;transition:opacity .3s;pointer-events:none
}
.overlay-dialog.visible{opacity:1;pointer-events:all
}
.overlay-dialog p{overflow-wrap:break-word;max-width:100%
}
