
#app{display:flex;flex-direction:column;overflow:hidden
}
#app .page-container{display:flex;flex-direction:column
}
#app>div{flex:1
}
#app>div:last-child{flex:0 0 auto
}
.sep{background:linear-gradient(90deg, #4B64FF, #F91162);height:1px
}
.sep.top{height:4px
}
.box{background-color:#fff
}
.page{background-color:#fff;height:100%;overflow:auto;display:flex;flex-direction:column;flex:100% 0 0
}
.page>*{flex:0 0 auto
}
.page .account-subpage{flex:1;display:flex;overflow:hidden
}
.scroll-view{flex:1;overflow-y:auto;position:relative
}
.form{margin:10px
}
.form .form-line{padding:5px 3px;border-bottom:1px solid #DFDFDF
}
.form .form-line:last-of-type{border-bottom:0
}
.form .form-line label{font-weight:500;display:flex;justify-content:space-between;align-items:center
}
.form-actions{display:flex;flex-direction:column;align-items:center;padding:10px 0 0
}
.text-input{border:1px solid #C6C6C6;border-radius:4px;line-height:22px;padding:0 6px;font-weight:normal
}
h2,strong{font-weight:500;font-size:1em
}
p{line-height:1.25
}
