
.fade-enter-active,.fade-leave-active{transition-duration:0.3s;transition-property:height, opacity;transition-timing-function:ease;overflow:hidden
}
.fade-enter,.fade-leave-active{opacity:0
}
.slide-left-enter-active,.slide-left-leave-active,.slide-right-enter-active,.slide-right-leave-active{transition-duration:0.4s;transition-property:height, opacity, -webkit-transform;transition-property:height, opacity, transform;transition-property:height, opacity, transform, -webkit-transform;transition-timing-function:cubic-bezier(0.55, 0, 0.1, 1);overflow:hidden
}
.slide-left-enter,.slide-right-leave-active{opacity:0;-webkit-transform:translate(2em, 0);transform:translate(2em, 0)
}
.slide-left-leave-active,.slide-right-enter{opacity:0;-webkit-transform:translate(-2em, 0);transform:translate(-2em, 0)
}
.slide-vertical-up-enter-active,.slide-vertical-up-leave-active,.slide-vertical-down-enter-active,.slide-vertical-down-leave-active{transition-duration:.35s;transition-property:height, opacity, -webkit-transform;transition-property:height, opacity, transform;transition-property:height, opacity, transform, -webkit-transform;transition-timing-function:cubic-bezier(0.55, 0, 0.1, 1);overflow:hidden
}
.slide-vertical-up-enter-to,.slide-vertical-down-enter-to{-webkit-transform:translate(0, -100%);transform:translate(0, -100%)
}
.slide-vertical-up-enter{-webkit-transform:translate(0, 0%);transform:translate(0, 0%)
}
.slide-vertical-down-leave-active{opacity:0;-webkit-transform:translate(0, 100%);transform:translate(0, 100%)
}
.slide-vertical-up-leave-active{opacity:0;-webkit-transform:translate(0, -100%);transform:translate(0, -100%)
}
.slide-vertical-down-enter{opacity:0;-webkit-transform:translate(0, -200%);transform:translate(0, -200%)
}
.zoom-enter-active,.zoom-leave-active{-webkit-animation-duration:0.4s;animation-duration:0.4s;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-animation-name:zoom;animation-name:zoom
}
.zoom-leave-active{animation-direction:reverse
}
@-webkit-keyframes zoom{
from{opacity:0;-webkit-transform:scale3d(0.3, 0.3, 0.3);transform:scale3d(0.3, 0.3, 0.3)
}
100%{opacity:1
}
}
@keyframes zoom{
from{opacity:0;-webkit-transform:scale3d(0.3, 0.3, 0.3);transform:scale3d(0.3, 0.3, 0.3)
}
100%{opacity:1
}
}
