
.btn{border:1px solid #aaa;line-height:22px;border-radius:100px;font-weight:500;font-size:.91667em;text-align:center;padding:0 5em;cursor:pointer
}
.btn:focus{outline:none;box-shadow:#F8105F 0 0 6px
}
.btn.primary{background-color:#F8105F;border-color:#F8105F;color:#FFFFFF
}
.btn.secondary{background-color:transparent;border-color:transparent;color:#999
}
.btn+.btn{margin-top:5px
}
