
.account-list-header{text-align:center;color:#F81264;font-size:.91667em;line-height:22px;border-bottom:1px solid #DFDFDF
}
.account-list{margin:0;padding:0;list-style:none
}
.account-list li{border-bottom:1px solid #DFDFDF
}
.account-list li a{display:block;text-decoration:none;color:inherit;padding:12px 15px;overflow:auto
}
.account-list li:hover{background-color:#f7f7f7
}
.add-account{color:#F81264;background:url(data:image/svg+xml;base64,) 4px 50%;background-size:23px 23px;background-repeat:no-repeat;padding-left:38px
}
.add-account.account-item{line-height:23px
}
